<template>
  <div class="main">
    <h3>Qualidade vs. Velocidade</h3>
    <a href="#" v-scroll-to="'#introduction'" style="float: right"
      >Voltar ao topo</a
    >

    <p><i>Postado em 29 de janeiro de 2021</i></p>

    <img
      alt="Vue logo"
      title="À esquerda, camadas de menor altura e melhor resolução; à direita, camadas mais altas e menor detalhamento"
      src="@/assets/3dprintweek/camadas01.jpg"
      width="40%"
      style="
        float: right;
        margin-left: 20px;
        margin-top: 0px;
        max-width: 500px;
        min-width: 200px;
      "
    />
    <p>
      Com a orientação do objeto em relação à mesa de impressão definida, a
      altura de cada camada que será impressa é fator chave na qualidade dos
      detalhes do objeto. Quanto mais finas as camadas, mais detalhes podem ser
      capturados pelos movimentos da impressora, entretanto, mais camadas serão
      necessárias para imprimir o objeto, consequentemente, maior o tempo de
      impressão.
    </p>
    <p>
      Se qualidade não é um fator importante, por exemplo, para peças
      funcionais, pode-se usar camadas mais altas, para reduzir o tempo de
      impressão. A altura máxima que pode ser escolhida é no diâmetro do bico de
      impressão (quanto maior o diâmetro, maior altura de camada é possível
      atingir). Um cuidado que se deve tomar, é o equilibrio entre temperatura,
      altura da camada e a velocidade dos movimentos.
    </p>
    <p>
      Embora exista o risco de se perder qualidade nos detalhes, pode-se
      aumentar a velocidade de movimento dos motores que controlam as direções
      horizontais e verticais (X e Y) do hotend. Com a necessidade de depositar
      (extrusar) plástico derretido de forma mais rápida, eventualmente a
      temperatura precisa ser calibrada (aumentar a temperatura para melhorar a
      fluidez do material). Esse equilíbrio dos parâmetros de velocidade,
      temperatura e altura de camada é o grande desafio do maker na hora da
      impressão algum objeto.
    </p>
    <p>
      As vezes, diferentes objetos, precisam de diferentes configurações, mesmo
      utilizando o mesmo material e a prática é a única forma de atingir bons
      resultados. Quanto mais uma pessoa conhecer as características únicas da
      sua impressora, melhor serão os objetos impressos.
    </p>

    <p>
      <a href="https://3dlab.com.br/saiba-configurar-a-primeira-camada/">
        Saiba mais com esse post da 3DLab (português)
      </a>
    </p>
