<template>
  <div class="main">
    <h3>Como a impressora FDM funciona?</h3><a href="#" v-scroll-to="'#introduction'" style="float:right">Voltar ao topo</a>

    <p><i>Postado em 31 de janeiro de 2021</i></p>

    <p>
      Explicando um pouco de como uma impressora 3D FDM normalmente funciona, em
      geral, existe um tubo metálico que pode ser aquecido (hotend) com um bico
      para saída do plástico.
    </p>
    <p>
      Existe um motor que controla a vazão de plástico que passa por esse hotend
      denominado "extrusor". Outros motores são responsáveis por controlar a
      posição horizontal (eixo X) e vertical (eixo Y) do bico em relação ao
      plano da mesa.
    </p>
    <p>
      Por fim, a altura do objeto é controlada pela quantidade de camadas de
      plástico que será depositada e, conforme a impressora vai construindo as
      camadas, um outro conjunto de motores faz com que o bico vá subindo (eixo
      Z).
    </p>
    <p>
      Curiosidade: Esse tipo de sistema de movimento da impressora é denominado
      "cartesiano", mas existem outros tipos de impressoras como as tipo Delta.
    </p>
  </div>
</template>

