<template>
  <div class="main">
    <h3>Modelagem</h3>
    <a href="#" v-scroll-to="'#introduction'" style="float: right"
      >Voltar ao topo</a
    >

    <p><i>Postado em 26 de janeiro de 2021</i></p>

    <img
      alt="Vue logo"
      src="@/assets/3dprintweek/modeling00.jpg"
      width="40%"
      style="
        float: left;
        margin-right: 20px;
        margin-top: 30px;
        max-width: 600px;
        min-width: 200px;
      "
    />
    <p>
      Normalmente, o processo inicia ao definir o objeto que a pessoa quer
      imprimir, seja qual for a finalidade. Por exemplo, uma pessoa pode querer
      um chaveiro novo em formato de estrela para colocar a chave de casa, ou
      talvez essa pessoa possua uma loja de decoração e gostaria produzir um
      vaso com design bem moderno para vender.
    </p>
    <p>
      O primeiro passo é conseguir o que chamamos de "Modelo 3D" do objeto, que
      nada mais é que a versão digital, criada no computador, que contém toda
      informação da forma desse objeto. O formato de arquivo mais comum
      encontrado é o STL (stereolithography file format) que representa as
      superfícies dos objetos na forma de pequenos triângulos.
    </p>
    <p>
      Existem duas formas de se obter o modelo 3D. O primeiro, é buscando em
      alguns sites dedicados como thingiverse.com, cgtrader.com, cults3d.com
      onde é possível buscar e fazer o download desses arquivos (alguns
      gratuitos, outros são pagos), nunca esquecendo de tomar cuidado com as
      condições na licença de uso do modelo definidas pelo autor (principalmente
      no caso de direitos de venda). A segunda forma, caso não encontre algum
      modelo que tenha gostado, ou a licença não permita o uso da maneira
      desejada, é "modelar" um novo objeto, ou seja, criar o seu próprio modelo.
      Nesse caso, habilidades de modelagem podem ser necessárias e existem
      diversos softwares (gratuitos, pagos, para PC, online...) que permitem a
      construção do objeto e a exportação em formato STL.
    </p>
    <p>
      Alguns exemplos de softwares são AutoCad (da empresa Autodesk), Blender
      (gratuito), OnShape (software de modelagem online:
      https://www.onshape.com/en/ que tem planos gratuitos e pagos dependendo do
      uso), tinkercad (software online gratuito da autodesk simples e intuitivo,
      recomendado para quem quer criar objetos simples sem precisar de muito
      conhecimento em modelagem 3D).
    </p>
    <p>
      Caso a pessoa não opte por modelar, é possível buscar modeladores
      freelancers (inclusive pela plataforma de serviços getNinjas) e pedir um
      orçamento para modelar o objeto (sempre uma boa prática avisar que o
      modelo será utilizado para ser impresso em 3D).
    </p>

