    <p>
      Com o material escolhido e a temperatura definida no software fatiador,
      deve-se definir a posição do objeto sobre a mesa de impressão. Geralmente,
      quanto maior a superfície em contato com a mesa, menor a chance do objeto
      descolar no meio da impressão (algo que acontece muito frequentemente).
    </p>
    
    <p>
      Além disso, deve-se atentar se o objeto, quando pronto, sofrerá estresse
      mecânico (por exemplo, um cabide que terá que suportar o peso da roupa),
      pois o objeto tem menos resistência a tensões que forçam a separação das
      camadas. A configuração de preenchimento (0 significa objeto oco e 100%
      objeto totalmente maciço) pode ajudar a dar mais resistência ao objeto,
      assim como a quantidade de paredes (perímetros), ao custo de mais material
      e tempo para impressão.
    </p>
    <img
      alt="Vue logo"
