    />

    <p>
      Fazendo uma analogia de uma impressora 3d para uma convencional, o
      filamento é como se fosse o cartucho da tinta.
    </p>

    <p>Ele é um fio enrolado num carretel que a impressora usa como insumo.</p>

    <p>Os filamentos mais comuns utilizados são compostos, em geral dos seguintes materiais:</p>

    <ul>
      <li>
        PLA, um polímero feito com ácido lático proveniente de matéria orgânica
        (milho, mandioca, cana de açúcar, beterraba), é compostável e
        biodegradável, tem baixa temperatura de fusão (impressão a 200ºC) e pode
        deformar se ficar exposto ao sol por longos períodos;
      </li>
      <li>
        ABS, um plástico derivado do petróleo muito utilizado na indústria pelo
        seu custo relativamente baixo e propriedades de resistência e
        flexibilidade (um grande exemplo são as peças do jogo Lego), não é tão
        ecológico quanto o PLA e com temperatura de fusão mais alta (230ºC) é um
        pouco mais difícil de imprimir, por se contrair ao esfriar as vezes
        formando rachaduras entre as camadas;
      </li>
      <li>
        PETg é um plástico que originado do famoso PET (muito utilizado em
        garrafas rígidas para bebidas) e o glicol que traz como característica a
        redução do seu ponto de fusão (ficando próximo do ponto de fusão do ABS,
        em torno de 240ºC). Traz as propriedades mecânicas e resistência do ABS
        e a estabilidade térmica do PLA, sofrendo menos contração em comparação
        ao ABS sendo, entretanto, o mais caro dentre os três;
      </li>
      <li>
        TPU, ou flexível, é um plástico com propriedades elásticas de grande
        resistência abrasiva, sendo provavelmente um dos mais dífíceis de
        imprimir em comparação com os outros três plásticos. Os objetos
        impressos com esse material ficam flexíveis e elásticos, como rodinhas
        de borracha de carrinhos de brinquedo.
      </li>
    </ul>

    <p>
      Existem diversos outros materiais e combinações de materiais, como PLA
      misturado com fibras de madeira, ou com particular de metais como cobre,
      que inclusive trazem um pouco de propriedades condutivas e de brilho após
      polimento.
    </p>

