<template>
  <div class="main">
    <h3>A Evolução da impressão 3D</h3><a href="#" v-scroll-to="'#introduction'" style="float:right">Voltar ao topo</a>

    <p><i>Postado em 25 de janeiro de 2021</i></p>
    
    
    <img
      alt="Vue logo"
      src="@/assets/3dprintweek/evolution00.jpg"
      width="40%"
      
      style="float: right; margin-left:20px; margin-top:30px; max-width: 600px; min-width: 200px"
    />
    <p>
      Embora a tecnologia tenha se popularizado na década de 2010, o conceito de
      equipamentos de manufatura aditiva (nome original da tecnologia) surgiu
      entre as décadas de 70 e 80. Na época vários pesquisadores entraram com
      pedidos de patentes de diversos sistemas que são utilizados até hoje de
      impressão 3D. Apenas em 2009, com a queda de algumas dessas patentes, é
      que a tecnologia começou a ficar mais acessível e equipamentos de escala
      não industrial começaram a ser criados e vendidos para os entusiastas da
      cultura maker (o DIY, ou em português "faça você mesmo").
    </p>
    <p>
      Assim, os tipos mais populares de impressão 3d "caseira" são o SLA, onde
      utiliza-se um polímero fotosensível (resina) para compor cada camada do
      objeto que é curada por uma luz ultra-violeta (no caso o UV é o mais
      encontrado, embora existam também resinas que são curadas com outros tipos
      de radiação, ou compostos químicos), e o FDM, onde funde-se um polímero
      termoplástico que é depositado camada por camada.
    </p>
    <p>
      Existem impressoras que usam outras tecnologias, como lasers para fundir
      materiais em pó, impressoras que fundem e produzem objetos de metais como
      alumínio ou titânio (muito usado para órteses por serem biocompatíveis), e
      alguns pesquisadores já utilizam tecnologia semelhante para imprimir
      tecidos orgânicos (no caso utilizando materiais especiais e
      próprios para isso, normalmente chamados de biotinta).
    </p>
    <p>
      Independente do material e da forma, a tecnologia da manufatura aditiva
      tem como principal característica a produção de objetos através da
      superposição de camadas.
    </p>
    
